/**
 * Scope de recherche sur tout l'atelier.
 */
export class TaskCritFullWsp extends UiCritBase {

	static AREA = new UiCritArea("wsp-taskcrit-fullwsp")
		.setLabel("TÃ¢ches de l'atelier")
		.setCritSgn("scope").setReplacePattern(/\bscope\b/)
		.setGroup("scope")
		.setBuildAbstract(function (this: UiCritArea, xml: Element, ctx: OUiCritInit) {
			const actStages = xml.getAttribute("actStages")?.split(" ");
			const actStagesTitles = [];
			if (actStages?.indexOf("completed") > -1) actStagesTitles.push("close");
			if (actStages?.indexOf("pending") > -1) actStagesTitles.push("en cours");
			if (actStages?.indexOf("forthcoming") > -1) actStagesTitles.push("Ã  venir");
			const stages = actStagesTitles.join(', ');
			// FIXME : periodOp
			return <span class="crit">TÃ¢ches de l'atelier ({actStagesTitles.join(', ')})</span>;
		});

	protected _pending: HTMLInputElement;
	protected _completed: HTMLInputElement;
	protected _forthcoming: HTMLInputElement;

	get withStageCompleted(): boolean {return this._completed.checked}

	get withStagePending(): boolean {return this._pending.checked}

	get withStageForthcoming(): boolean {return this._forthcoming.checked}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const wsp = this.reg.env.wsp;
		const sr = this.shadowRoot;
		sr.appendChild(<div class="critHead">
			<span class="critTitle">TÃ¢ches de l'atelier</span>
			{this.insertAlternativesBtn(init)}
		</div>);
		let optsRoot = sr.appendChild(<div class="h"/>) as HTMLDivElement;
		this._pending = optsRoot.appendChild(<label class="opt"><input type="checkbox" checked onclick={this.onChange}/>en cours</label>).firstElementChild as HTMLInputElement;
		this._completed = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>closes</label>).firstElementChild as HTMLInputElement;
		this._forthcoming = optsRoot.appendChild(<label class="opt"><input type="checkbox" checked onclick={this.onChange}/>Ã  venir</label>).firstElementChild as HTMLInputElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const actStages = xml.getAttribute("actStages")?.split(" ");
		this._pending.checked = actStages?.indexOf("pending") > -1;
		this._completed.checked = actStages?.indexOf("completed") > -1;
		this._forthcoming.checked = actStages?.indexOf("forthcoming") > -1;
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const stages: EActStage[] = [];
		if (this.withStagePending) stages.push("pending");
		if (this.withStageCompleted) stages.push("completed");
		if (this.withStageForthcoming) stages.push("forthcoming");
		return new SearchTasksAll(stages);
	}

	buildUiXmlCrit(): Element | undefined {
		const crit = this.buildCrit().toDom();
		crit.setAttribute("uiCrit", this.getUiCritXmlAtt());
		return crit;
	}
}
customElements.define("wsp-taskcrit-fullwsp", TaskCritFullWsp);
REG.reg.registerSvc("wsp-taskcrit-fullwsp", 1, TaskCritFullWsp.AREA);
REG.reg.addSvcToList("wsp.crit.tasks", "wsp-taskcrit-fullwsp", 1, "wsp-taskcrit-fullwsp");


/**
 * Scope de recherche pour des users donnÃ©s
 */
export class TaskCritByUsers extends UiCritBase {

	static AREA = new UiCritArea("wsp-taskcrit-byusers")
		.setLabel("TÃ¢ches des utilisateurs")
		.setGroup("scope")
		.setCritSgn("scope").setReplacePattern(/\bscope\b/)
		.setBuildAbstract(function (this: UiCritArea, xml: Element, ctx: OUiCritInit) {
			// - actStages
			const actStages = xml.getAttribute("actStages")?.split(" ");
			const actStagesTitles = [];
			if (actStages.indexOf("completed") > -1) actStagesTitles.push("close");
			if (actStages.indexOf("pending") > -1) actStagesTitles.push("en cours");
			if (actStages.indexOf("forthcoming") > -1) actStagesTitles.push("Ã  venir");
			const stages = actStagesTitles.join(', ');
			// users
			let usersListVal: HTMLElement;
			const users = xml.getAttribute("users");
			if (users) {
				const accounts = users.split(" ");
				const usersObj = <span/>;
				accounts.forEach((account, pos) => {
					if (pos > 0)
						usersObj.appendChild(<span> ou </span>);
					usersObj.appendChild(<UserRef class="inline list" Ã®={{reg: ctx.reg, nickOrAccount: account, withIcon: false} as OUserRefInit}/>)
				});
				usersListVal = <span>'{usersObj}'</span>;

				const expandUsers = xml.getAttribute("expandUsers")?.split(" ");
				if (expandUsers?.indexOf("toGroups") && expandUsers.indexOf("toMembers"))
					usersListVal.appendChild(<span> (Ã©tendu aux membres/groupes)</span>);
				if (expandUsers?.indexOf("toGroups"))
					usersListVal.appendChild(<span> (Ã©tendu aux groupes)</span>);
				if (expandUsers?.indexOf("toMembers"))
					usersListVal.appendChild(<span> (Ã©tendu aux membres)</span>);
			}
			// Resps
			const modelResps = ctx.reg.env.wsp.wspMetaUi.listResps('task');
			const respsArr = xml.getAttribute("responsibilities")?.split(" ");
			const respsLabels: string[] = [];
			if (respsArr) respsArr.forEach((resp) => {
				const currentResp = modelResps.find((entry) => entry.code === resp);
				respsLabels.push(currentResp ? currentResp.name : resp);
			});
			let respsVal: HTMLElement;

			if (respsLabels.length == 1) {
				const resp = respsLabels[0];
				respsVal = <span>{`(ayant la responsabilitÃ© '${resp}')`}</span>;
			} else if (respsLabels.length > 1) {
				const resps = respsLabels.join(" ou ");
				respsVal = <span>{`(ayant l'une des responsabilitÃ©s '${resps}')`}</span>;
			}

			// FIXME : l10n
			return <span class="crit">TÃ¢ches des utilisateurs {usersListVal} {respsVal} dans l'un des Ã©tats '{actStagesTitles.join(', ')}'</span>;
		});

	protected _userElt: InputUserPanel<any>;
	protected _toGroupsElt: HTMLInputElement;
	protected _toMembersElt: HTMLInputElement;

	protected _pending: HTMLInputElement;
	protected _completed: HTMLInputElement;
	protected _forthcoming: HTMLInputElement;

	protected _respElt: HTMLSelectElement;

	get withStageCompleted(): boolean {return this._completed.checked}

	get withStagePending(): boolean {return this._pending.checked}

	get withStageForthcoming(): boolean {return this._forthcoming.checked}

	get withExpandUsersToGroups(): boolean {return this._toGroupsElt?.checked}

	get withExpandUsersToMembers(): boolean {return this._toMembersElt?.checked}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		const withGroups = this.reg.env.universe.useUsers.hasAspect(EUserAspects.groupable);

		// users
		this._userElt = new InputUserPanel().initialize({
			name: "choiceUser",
			reg: this.reg,
			emptySelectionMsg: withGroups ? "Utilisateurs/groupes..." : "Utilisateurs...",
			userCard: "multi",
			usersGridInit: {
				usersSrv: this.reg.env.universe.useUsers,
				filterTypeInputVisibility: true,
				filterType: null,
			},
		} as OInputUserInit<any>) as InputUserPanel<any>;
		this._userElt.addEventListener("change", this.onChange);

		let critGroups;
		if (withGroups) {
			this._toGroupsElt = <input type="checkbox" onclick={this.onChange}/> as HTMLInputElement;
			this._toMembersElt = <input type="checkbox" onclick={this.onChange}/> as HTMLInputElement;
			critGroups = <div class="h">
				<label class="opt" title="Inclure les items associÃ©s aux groupes auxquels les utilisateurs sÃ©lectionnÃ©s appartiennent">{this._toGroupsElt}+ groupes</label>
				<label class="opt" title="Inclure les items associÃ©s aux membres des groupes sÃ©lectionnÃ©s">{this._toMembersElt}+ membres</label>
			</div>;
		}

		sr.appendChild(<div class="critHead">
			<span class="critTitle">TÃ¢ches des utilisateurs</span>
			<div class="inputsCtn">
				{this._userElt}
				{critGroups}
			</div>
			{this.insertAlternativesBtn(init)}
		</div>);

		// actStage
		let optsRoot = sr.appendChild(<div class="h"/>) as HTMLDivElement;
		this._pending = optsRoot.appendChild(<label class="opt"><input type="checkbox" checked onclick={this.onChange}/>en cours</label>).firstElementChild as HTMLInputElement;
		this._completed = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>closes</label>).firstElementChild as HTMLInputElement;
		this._forthcoming = optsRoot.appendChild(<label class="opt"><input type="checkbox" checked onclick={this.onChange}/>Ã  venir</label>).firstElementChild as HTMLInputElement;

		// resp
		/* non implÃ©menter cotÃ© java : eu.scenari.wspodb.search.execexp.TasksByUsersExecExp.filterResps
				CrirÃ¨re dÃ©diÃ© utilisÃ©
		this._respElt = <select onchange={this.onChange} class="respSelect" multiple="true"/> as HTMLSelectElement;
		this.reg.env.wsp.wspMetaUi.hasForthcomingTasks()
		const resps = this.reg.env.wsp.wspMetaUi.listResps('task');
		resps?.forEach((resp) => {
			let opt = this._respElt.appendChild(<option value={resp.code}>{resp.name}</option>);
			if(resp.desc) opt.title = resp.desc;
		});
		sr.appendChild(
			<div class="h">
				<div class="v usersBox">
					<span>ResponsabilitÃ©</span>
					{this._respElt}
				</div>
			</div>
		);
		 */
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		// users
		const users = xml.getAttribute("users")?.split(" ");
		this._userElt.value = users;
		const expandUsers = xml.getAttribute("expandUsers")?.split(" ");
		this._toGroupsElt.checked = expandUsers?.indexOf("toGroups") > -1;
		this._toMembersElt.checked = expandUsers?.indexOf("toMembers") > -1;

		// actStages
		const actStages = xml.getAttribute("actStages")?.split(" ");
		this._pending.checked = actStages?.indexOf("pending") > -1;
		this._completed.checked = actStages?.indexOf("completed") > -1;
		this._forthcoming.checked = actStages?.indexOf("forthcoming") > -1;

		// resps
		const responsibilities = xml.getAttribute("responsibilities")?.split(" ");
		if (responsibilities && this._respElt) {
			for (const opt of this._respElt.options)
				opt.selected = responsibilities.includes(opt.value)

		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		// actStages
		const stages: EActStage[] = [];
		if (this.withStagePending) stages.push("pending");
		if (this.withStageCompleted) stages.push("completed");
		if (this.withStageForthcoming) stages.push("forthcoming");
		// resps
		const resps = [];
		if (this._respElt)
			for (const opt of this._respElt.options)
				resps.push(opt.value)

		const crit = new SearchTasksByUsers(this._userElt.value, stages)
			.setExpandToMembers(this._toMembersElt?.checked)
			.setExpandToGroups(this._toGroupsElt?.checked)
			.setResps(resps);
		return crit || new SearchTasksAll(stages);
	}

}

customElements.define("wsp-taskcrit-byusers", TaskCritByUsers);
REG.reg.registerSvc("wsp-taskcrit-byusers", 1, TaskCritByUsers.AREA);
