/**
 * Type de la tache
 *    <wsp-taskcrit-type/>
 */
export class TaskCritType extends UiCritBase {

	static AREA = new UiCritArea("wsp-taskcrit-type").setLabel("Type")
		.setCritSgn("type").setGroup("task").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const itModels = xml ? xml.getAttribute('uiItModels') : null;
			const itLabels: string[] = [];
			if (itModels != null) {
				const itTypesArray = itModels.split("|");
				itTypesArray.forEach(itModel => {
					itLabels.push(ctx.reg.env.wsp.wspMetaUi.getItemType(itModel).getTitle());
				});
			}
			if (itLabels.length == 1) {
				const itLabel = <span class="critVal">{itLabels[0]}</span>;
				return <span class="crit">Type '{itLabel}'</span>;
			} else if (itLabels.length < 5) {
				return <span class="crit">Types '{itLabels.join(', ')}'</span>;
			} else {
				const itCount = <span class="critVal">{itLabels.length}</span>;
				return <span class="crit" title={itLabels.join(", ")}>{itCount} types</span>;
			}
		});

	protected _typesElt: HTMLSelectElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		const it = this.reg.env.wsp.wspMetaUi.getItemTypes();

		this._typesElt = <select onchange={this.onChange} multiple="true"/> as HTMLSelectElement;
		it.forEach((entry) => {
			if (entry.isFamily(EItemTypeFamily.task)) {
				const opt = this._typesElt.appendChild(<option value={entry.getModel()}>{entry.getTitle()}</option>);
				if (entry.getIcon()) opt.style.backgroundImage = `url("${entry.getIcon()}")`;
			}
		});

		const critHead = sr.appendChild(<div class="critHead">
			<span class="critTitle">Type(s) :</span>
			{this._typesElt}
			{this.insertAlternativesBtn(init)}
		</div>);

	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this._typesElt.selectedIndex = -1;
		const itModelsArray = xml?.getAttribute('uiItModels')?.split("|") || [];
		for (const opt of this._typesElt.options)
			opt.selected = itModelsArray.indexOf(opt.value) > -1 ? true : false;
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this._typesElt.selectedOptions)
			return new SearchItemSgnRegexp(Array.from(this._typesElt.selectedOptions).map(entry => '@' + LANG.escape4Regexp(entry.value) + "\\b.*"));
	}

	/**
	 *  CritÃ¨res UI :
	 *    uiItModels : liste des itModels sÃ©lectionnÃ©s, sÃ©papÃ©s par un |
	 */
	buildUiXmlCrit(): Element | undefined {
		const selectedItModels: string[] = [];
		for (const opt of this._typesElt.selectedOptions)
			if (opt.selected) selectedItModels.push(opt.value)
		const crit: Element = super.buildUiXmlCrit();
		if (crit && selectedItModels) crit.setAttribute("uiItModels", selectedItModels.join("|"));
		return crit;
	}

}
customElements.define("wsp-taskcrit-type", TaskCritType);
