/**
 * Deadline de la tache
 *    <wsp-taskcrit-deadline/>
 */
export class TaskCritDeadline extends UiCritBase {

	static AREA = new UiCritArea("wsp-taskcrit-deadline")
		.setLabel("Date limite").setReplacePattern(/\bdate\b/).setGroup("task")
		.setVisible((ctx) => {
			const wspMetaUi = ctx.reg.env.wsp.wspMetaUi;
			const tasksIt: ItemTypeTask[] = wspMetaUi.getItemTypes().filter((entry) => entry.isFamily(EItemTypeFamily.task)) as ItemTypeTask[];
			return tasksIt?.find(entry => entry.hasFeature(ETaskFeatures.deadline)) ? true : false;
		})
		.setCritSgn("type").setGroup("task").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			//FIXME : afficher une forme lisible de la date spÃ©cifiÃ©e
			const modes = InputPeriod.buildDefaultModes_futurAndPast();
			const uiCurrentModeId = xml?.getAttribute("uiCurrentMode");
			const uiCurrentMode: InputPeriodAction = uiCurrentModeId ? modes.find((entry) => entry.getId() === uiCurrentModeId) as InputPeriodAction : null;
			const uiCurrentModeLabel = uiCurrentMode?.selectedLabel || uiCurrentMode?.getLabel(null);
			return uiCurrentModeLabel ? <span class="crit"><span class="critTitle">Date limite de type</span> '<span class="critVal">{uiCurrentModeLabel}</span>'</span> : <span class="crit"><span class="critTitle">Date limite</span></span>;
		});

	protected _dateElt: InputPeriod;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this._dateElt = new InputPeriod().initialize({defaultLabel: "Choisir une valeur...", modes: InputPeriod.buildDefaultModes_futurAndPast()});
		this._dateElt.addEventListener("change", this.onChange);
		this._dateElt.addEventListener("input", this.onChange);
		const icon = this.critArea ? this.critArea.getIcon({reg: this.reg}) : null;
		const img = icon ? <img src={icon}/> : null;
		const head = sr.appendChild(<div class="critHead">
			{img}
			<span class="critTitle">{this.critArea ? this.critArea.getLabel({reg: this.reg}) : ""}</span>
			{this._dateElt}
			{this.insertAlternativesBtn(init)}
		</div>);
		UiCritBase.initHeadFromArea(this.critArea, {reg: this.reg}, head);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this._dateElt.reset(xml.getAttribute("uiCurrentMode"), xml.hasAttribute("uiCurrentLess") ? Number.parseInt(xml.getAttribute("uiCurrentLess")) : null, xml.hasAttribute("uiCurrentMore") ? Number.parseInt(xml.getAttribute("uiCurrentMore")) : null);
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this._dateElt.isSpecified())
			return SearchTasksDeadline.buildFromInputPeriod(this._dateElt);
		return undefined;
	}

	/**
	 *  CritÃ¨res UI :
	 *    uiCurrentMode, uiCurrentLess, uiCurrentMore
	 */
	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit) {
			if (crit && this._dateElt.isSpecified()) {
				crit.setAttribute("uiCurrentMode", this._dateElt.currentMode.getId());
				// Valeurs brutes (sans les regles d'exclusion/normalisation qui sont fournies par le "currentMode")
				if (this._dateElt.date1.valueAsNumber) crit.setAttribute("uiCurrentLess", this._dateElt.date1.valueAsNumber.toString());
				if (this._dateElt.date2.valueAsNumber) crit.setAttribute("uiCurrentMore", this._dateElt.date2.valueAsNumber.toString());
			}
		}
		return crit;
	}

}
customElements.define("wsp-taskcrit-deadline", TaskCritDeadline);
