/**
 * Cycle de vie de la tache
 *    <wsp-taskcrit-lifecycle/>
 */
export class TaskCritLifeCycle extends UiCritBase {

	static AREA = new UiCritArea("wsp-taskcrit-lifecycle")
		.setLabel((ctx) => ctx.reg.env.wsp.wspMetaUi.getLcName())
		.setVisible((ctx) => {
			const wspMetaUi = ctx.reg.env.wsp.wspMetaUi;
			const tasksIt: ItemTypeTask[] = wspMetaUi.getItemTypes().filter((entry) => entry.isFamily(EItemTypeFamily.task)) as ItemTypeTask[];
			return tasksIt?.find(entry => entry.getLcStates()?.size) ? true : false;
		})
		.setCritSgn("lifecycle").setGroup("task").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const lcName = LC_DEFAULT_NAME;
			const uiCritStatesByModelsStr = xml.getAttribute("uiCritStatesByModels");
			if (uiCritStatesByModelsStr && uiCritStatesByModelsStr.length) {
				const wspMetaUi = ctx.reg.env.wsp.wspMetaUi;
				const uiCritStatesByModels = uiCritStatesByModelsStr.split(" ");

				function makeLcLabelEntry(itModel: string, lcCode: string): string {
					const it = wspMetaUi.getItemType(itModel) as ItemTypeTask;
					const lc = it?.getLcStateOrUnknown(lcCode);
					return lc.name || lcCode;
				}

				if (uiCritStatesByModels.length == 1) {
					const args = uiCritStatesByModels[0].split(":");
					<span class="crit">Ãtat du {lcName} : <span class="critVal">{makeLcLabelEntry(args[0], args[1])}</span></span>
				} else {
					const vals = new Set<string>();
					uiCritStatesByModels.forEach((entry) => {
						const args = entry.split(":");
						vals.add(makeLcLabelEntry(args[0], args[1]));
					});
					return <span class="crit">Ãtats du {lcName} : <span class="critVal">{Array.from(vals).sort().join(", ")}</span></span>
				}
			} else {
				const lcStates = xml.getAttribute("lcStates") || "";
				const lcStatesArr = lcStates.split(" ");
				const lcStatesLabels = lcStatesArr.join(", ");
				return <span class="crit">Ãtat(s) du {lcName} : <span class="critVal">{lcStatesLabels}</span></span>;
			}
		});

	protected _lcElt: HTMLSelectElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this.reg.installSkin("wsp-taskcrit-lifecycle", sr);

		this._lcElt = <select onchange={this.onChange} class="lcSelect" multiple="true"/> as HTMLSelectElement;

		const wspMetaUi = this.reg.env.wsp.wspMetaUi;
		const tasksIt: ItemTypeTask[] = wspMetaUi.getItemTypes().filter((entry) => entry.isFamily(EItemTypeFamily.task)) as ItemTypeTask[];
		tasksIt.forEach((it) => {
			const lcs = it.getLcStates();
			if (lcs.size) {
				let optFrp = this._lcElt.appendChild(this._lcElt.appendChild(<optgroup label={it.getTitle()}/>));
				lcs.forEach((lc) => {
					let opt = optFrp.appendChild(<option value={lc.code || LC_STATE_DEFAULT_KEY} data-model={it.getModel()}>{lc.name || LC_STATE_DEFAULT_NAME}</option>);
					opt.style.color = lc.color || "var(--color)";
					if (lc.iconUrl) opt.style.backgroundImage = `url("${lc.iconUrl}")`;
					if (lc.code == "") DOM.setAttrBool(opt, "selected", true);
				});

			}
		});

		const critHead = sr.appendChild(<div class="critHead">
			<div class="h">
				<span class="critTitle">{this.reg.env.wsp.wspMetaUi.getLcName()}</span>
				{this._lcElt}
			</div>
			{this.insertAlternativesBtn(init)}
		</div>);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		// LC
		this._lcElt.selectedIndex = -1;
		const lcStates = xml.getAttribute("lcStates") || "";
		const lcStatesArr = lcStates.split(" ");
		Array.from(this._lcElt.options).forEach(opt => {
			opt.selected = lcStatesArr.includes(opt.value)
		})
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		let crit = new SearchLifeCycleStates();
		crit.lcStates = [];
		Array.from(this._lcElt.selectedOptions).forEach(opt => crit.lcStates.push(opt.value));
		return crit;
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit) {
			const statesWithModel: string[] = [];
			Array.from(this._lcElt.selectedOptions).forEach(opt => statesWithModel.push(opt.getAttribute('data-model') + ":" + opt.value));
			crit.setAttribute("uiCritStatesByModels", statesWithModel.join(" "));
		}
		return crit;
	}

}

